<script>
import { fetchArtists } from '@/service/api';

export default {
  name: 'ArtistList',
  data() {
    return {
      keyword: '',
      artistList: [],
    };
  },
  created() {
    this.getArtists();
  },
  methods: {
    getArtists() {
      fetchArtists()
        .then((res) => {
          this.artistList = res.data.data;
        })
        .catch((error) => {
          console.error('fetchArtistList錯誤：', error);
        });
    },
  },
  computed: {
    filteredArtist() {
      return this.artistList
        .filter((x) => x.artist_info.name.includes(this.keyword));
    },
  },
};
</script>

